import React, { Suspense, lazy, useState, useEffect } from "react";
import { Switch, Route, withRouter } from "react-router-dom";
import AllTime from './all-time';
import Individual from './individual';
import Weekly from './weekly';
import {TransitionGroup, CSSTransition} from "react-transition-group";
import '../css/style.css';
import "bootstrap/dist/css/bootstrap.min.css";
import classes from '../css/BackgroundVideo.module.css'
import stdimm from '../assets/st-dimm.mp4'
import sfii from '../assets/sfii.mp4'
import doom from '../assets/doom.mp4'


var combo = ''

// Hook

function useKeyPress(targetKey) {

  // State for keeping track of whether key is pressed
  const [keyPressed, setKeyPressed] = useState(false);

  // If pressed key is our target key then set to true
  

  function downHandler({ key }) {
    if (key === targetKey) {
      setKeyPressed(true);
      combo += key;
    }
  }
  // If released key is our target key then set to false

  const upHandler = ({ key }) => {
    if (key === targetKey) {
      setKeyPressed(false);
    }

  };
  // Add event listeners

  useEffect(() => {
      
    window.addEventListener('keydown', downHandler);
    window.addEventListener('keyup', upHandler);

    // Remove event listeners on cleanup
    return () => {
      window.removeEventListener('keydown', downHandler);
      window.removeEventListener('keyup', upHandler);

    };
  }, []); // Empty array ensures that effect is only run on mount and unmount

  return keyPressed;
}

//{combo === 'ArrowUpArrowUpArrowDownArrowDownArrowLeftArrowRightArrowLeftArrowRightba' && 'cheat mode activated!'}

const videosrc = () => {
    if (combo === 'ArrowUpArrowUpArrowDownArrowDownArrowLeftArrowRightArrowLeftArrowRightba'){
        return sfii
    }
    else {
        return stdimm
    }
}



const change = () => {
    var video = document.getElementById("twain")
    var source = document.createElement('source');

    video.pause()

    video.setAttribute('src', sfii);  
    video.load();
    video.play();
    setVolume(.8)
}

const changeDoom = () => {
  var video = document.getElementById("twain")
  var source = document.createElement('source');

  video.pause()

  video.setAttribute('src', doom);  
  video.load();
  video.play();
  setVolume(.8)
}

function setVolume(volume)
{
    var video = document.getElementById("twain")
    
    if (video.muted) {
      video.muted = !video.muted
    }
    video.volume = volume 
  
}

    

var Container = ({location}) => {


const upPress = useKeyPress('ArrowUp');
const downPress = useKeyPress('ArrowDown');
const leftPress = useKeyPress('ArrowLeft');
const rightPress = useKeyPress('ArrowRight');
const bPress = useKeyPress('b');
const aPress = useKeyPress('a');

const iPress = useKeyPress('i');
const dPress = useKeyPress('d');
const qPress = useKeyPress('q');


    return (        
        <div>
{combo === 'ArrowUpArrowUpArrowDownArrowDownArrowLeftArrowRightArrowLeftArrowRightba' && change()}
{combo === 'iddqd' && changeDoom()}
               
             <video autoPlay="autoplay" loop="loop" muted className={classes.Video} id="twain" src={stdimm}>               
            </video>
           
            <TransitionGroup className="transition-group">
                <CSSTransition key={location.key} timeout={{enter: 150, exit: 0}} classNames={'fade'}>
                    <section className="route-section">
                        <Switch key={location.key} location={location}>                
                            <Route path="/all-time" component={AllTime} />
                            <Route path="/individual" component={Individual} />
                            <Route path="/weekly" component={Weekly} />
                            <Route path="/">
                                <AllTime />           
                            </Route>                            
                        </Switch>
                    </section>
                    </CSSTransition>
            </TransitionGroup>
        </div>  
    );
}


export default withRouter(Container);